<template>
    <div>
        <div class="text-center mx-auto font-bold">
            <div class="mb-5">
                <h1 class="text-white text-5xl title">To Do List</h1>
            </div>
        </div>
        <div class="cardDivInner w-10/12 sm:w-10/12 lg:w-1/2 text-center mx-auto border border-zinc-500 rounded shadow-lg  font-bold p-5">
            <div class="text-gray-500 text-right">
                <button @click="resetLocal">Reset LocalStorage</button>
            </div>

            <div class="Date">
                <p class="text-gray-500">{{date}}</p>
            </div>
            <div class="mt-6">
                <div class="">
                    <input type="text" @keyup.enter="addTodo" class="focus:border-gray-500 focus:ring-0 focus:offset-0 addTodo text-gray-title border border-gray-500 rounded-lg text-left px-3 py-2 drop-shadow-2xl bg-transparent w-full" placeholder="+ Add To List">
                </div>

                <div>
                    <div>
                        <h1 class="text-white text-left my-5 text-lg">Tasks</h1>

                        <div v-for="(item, index) in todos" :key="item.id" class="my-3"  >
                            <div :class="{'bg-red-500': item.color === 'red', 'bg-yellow-500': item.color === 'yellow', 'bg-green-500': item.color === 'green'}" class="rounded transition ease-in-out delay-200 duraiton-200 flex align-middle justify-between">

                                <div id="checklist" @click="addCompleted(index)" class="w-1/2 break-all text-white transition duration-200 ease-out text-1xl text-left my-5 transition ease-in-out delay-150   duration-200 cursor-pointer flex justify-start align-center">
                                    <input v-bind:id="item.id" type="checkbox" name="checkbox"  class="outline-0 offset-0 ring-0 border-0 focus:outline-0 focus:ring-0 focus:offset-0 focus:border-0 focus:ring-offset-0"/>
                                    <label v-bind:for="item.id">{{ item.text }}</label>
                                </div>

                                <div class="priority flex items-center justify-between text-white">
                                    <div @click="priority_red(index)" class="red rounded-full bg-red-500 p-2 " ></div>
                                    <div @click="priority_yellow(index)" class="yellow rounded-full bg-yellow-500 p-2 mx-2" ></div>
                                    <div @click="priority_green(index)" class="green rounded-full bg-green-500 p-2 " ></div>
                                </div>

                                <span class="X-icon mr-2  items-center justify-end inline-flex cursor-pointer" @click="removeTodo(index)">
                                    <svg class="fill-current text-red-500" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"/><path d="M0 0h24v24H0z" fill="none"/></svg>
                                </span>
                            </div>
                        </div>
                    </div>

                    <div>
                        <h1 class="text-white text-left my-5 text-lg">Completed</h1>

                        <div v-for="(item, index) in completed" :key="item.id" class="transition ease-in-out delay-200 duraiton-200 flex align-middle">
                            <div id="checklist"  class="w-1/2 break-all text-white transition duration-200 ease-out text-1xl text-left my-5 transition ease-in-out delay-150   duration-200 cursor-pointer flex justify-start align-center">
                                <input v-bind:id="item.id" type="checkbox" name="checkbox"  class="outline-0 offset-0 ring-0 border-0 focus:outline-0 focus:ring-0 focus:offset-0 focus:border-0 focus:ring-offset-0"/>
                                <label v-bind:for="item.id">{{ item.text }}</label>
                            </div>

                            <span class="X-icon w-2/4 items-center justify-end inline-flex cursor-pointer" @click="removeCompleted(index)">
                                <svg class="fill-current text-red-500" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"/><path d="M0 0h24v24H0z" fill="none"/></svg>
                            </span>
                        </div>
                    </div>
                </div>
                
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'App',
        data() {
            return {
                todos: [
                    {
                        id: 1,
                        text: 'Learn Vue',
                        completed: false
                    },
                    {
                        id: 2,
                        text: 'Learn Vuex',
                        completed: false
                    },
                    {
                        id: 3,
                        text: 'Learn Vue Router',
                        completed: false
                    }
                ],
                completed: [
                    {
                        id: 4,
                        text: 'Learn Vue',
                        completed: true
                    }
                ],
                date: new Date().toLocaleDateString(),
            }
        },
        mounted() {
            this.getTodos();
        },
        methods: {
            getTodos() {
                if (localStorage.getItem('todos') && localStorage.getItem('completed')) {
                    this.todos = JSON.parse(localStorage.getItem('todos'));
                    this.completed = JSON.parse(localStorage.getItem('completed'));
                }
            },
            addTodo(e) {
                this.todos.push({
                    id: this.todos.length + 1,
                    text: e.target.value,
                    completed: false
                })
                e.target.value = '';
                this.saveTodos();
            },
            addCompleted(index) {
                this.todos[index].completed = true;
                this.completed.push(this.todos[index]);
                this.todos.splice(index, 1);
                this.saveTodos();
            },
            removeTodo(index) {
                this.todos.splice(index, 1);
                this.saveTodos();
            },
            removeCompleted(index) {
                this.completed.splice(index, 1);
                this.saveTodos();
            },

            resetLocal() {
                localStorage.clear();
            },
            priority_red(index){
                this.todos[index].color = 'red';
                this.saveTodos();
            },
            priority_yellow(index){
                this.todos[index].color = 'yellow';
                this.saveTodos();
            },
            priority_green(index){
                this.todos[index].color = 'green';
                this.saveTodos();
            },
            saveTodos() {
                localStorage.setItem('todos', JSON.stringify(this.todos));
                localStorage.setItem('completed', JSON.stringify(this.completed));
            },
        },

    }
</script>

<style lang="scss"  scoped> 

.red{
    background-color: red;
    box-shadow: 0 0 0 1px #202124;
}
.green{
    background-color: green;
    box-shadow: 0 0 0 1px #202124;

}
.yellow{
    background-color: yellow;
    box-shadow: 0 0 0 1px #202124;

}
.title{
    font-family: 'Pacifico', cursive;
}
.text-gray-title{
    // color: #868e96;
    color: #a3bdbe;
}
.text-gray-title::placeholder {
    color: #a3bdbe;
}

#checklist {

    --check: #4F29F0;
    --disabled: #C3C8DE;
    --width: 100px;
    --height: 140px;
    --border-radius: 10px;
    position: relative;
    label {
        color: var(--text);
        position: relative;
        display: grid;
        align-items: center;
        transition: color .3s ease;
        &::before,
        &::after {
            content:"";
            position: absolute;
        }
        &::before {
            height: 2px;
            width: 8px;
            left: -27px;
            background: var(--check);
            border-radius: 2px;
            transition: .5s ease; 
        }
        &:after {
            height: 4px;
            width: 4px;
            top: 8px;
            left: -25px;
            border-radius: 50%;
        }
    }
    input[type="checkbox"] {
        -webkit-appearance: none;
        -moz-appearance: none;
        position: relative;
        margin: 8px 15px 0 0;
        background: var(--background);
        &::before,
        &::after {
            content:"";
            position: absolute;
            height: 2px;
            top: 50%;
            background: var(--check);
            border-radius: 2px;
        }
        &::before {
            width: 0px;
            right: 60%;
            transform-origin: right bottom;
        }
        &::after {
            width: 0px;
            left: 40%;
            transform-origin: left bottom;
        }
        &:checked {
            + label {
                color: var(--disabled);
                &::before {
                    background: var(--disabled);
                    width: 110%;
                    left: -5px;
                }
            }
        }
    }
}


</style>